import './ContentRatings.css';
import { animated, useSpring } from '@react-spring/web';
import MyNavbar from '../MyNavbar';
import { Button, Row, Col, Container } from 'react-bootstrap';
import { useEffect } from 'react';


function ContentRatings() {
  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="ContentRatings">
      <MyNavbar active='content_ratings' />
      <animated.div style={fadein_props}>
        <h1 className='Title'>CONTENT RATINGS</h1>

        <p id='introParagraph'>
          Content ratings are present on my content to indicate the approximate age range at
          which a particular content may be suitable or yet too inappropriate for minors of
          a certain age range.
        </p>

        <hr style={{ color: 'whitesmoke', margin: '1vw' }}></hr>

        <p id='introParagraph'>
          The following content ratings are based on those of&nbsp;
          <a href='https://bbfc.co.uk' target="_blank" rel="noopener noreferrer" style={{ color: 'salmon' }}>
            British Board of Film Classification (BBFC)</a>.
          This page only contains a general overview of what can be expected from each rating.
          Click the button below for more detailed content rating guidelines.
        </p>

        <Button href="https://docs.google.com/document/d/1mNsA7U5pP_rh_4VNz2k7bnlRoFF89fExP2qTK_ZMR14/edit?usp=sharing"
          target="_blank" rel="noopener noreferrer" id='contentRatingGuidelinesButton'>
          CONTENT RATING GUIDELINES
        </Button><br /><br />

        <Container>
          <Row>
            <Col md={2}><img src={require('./content_ratings/04G.png')} alt='G' className='ratingLogo' /></Col>
            <Col md={10}>
              <h4><u>GENERAL</u></h4>
              <p className='ratingSummary'>
                G-rated content should be suitable for general audiences aged 4 - 5 and over. There
                may be a few uses of the mildest bad language, a few moments of discreet natural nudity, and 
                some scenes of mild violence, scary scenes, and background drinking and smoking. Any unsettling 
                or threatening scenes should be mild, short, and counterbalanced by mitigating factors such as 
                animated/cartoony graphics, comedy, unrealistic settings, and an overall reassuring positive outcome.
                The difference between right and wrong must be made clear.
              </p>
            </Col>
          </Row><br/>
          <Row>
            <Col md={2}><img src={require('./content_ratings/08PG.png')} alt='PG' className='ratingLogo' /></Col>
            <Col md={10}>
              <h4><u>PARENTAL GUIDANCE</u></h4>
              <p className='ratingSummary'>
                PG-rated content may not be suitable for children under the ages 8 - 9. Some mild language
                and sexually suggestive may begin to appear. Violence and frightening scenes should still be
                mild overall, although they will clearly be more intense or prolonged than at G. There may be
                some more scenes related to consuming alcohol, tobacco, drugs, and other substances, though
                these scenes will not portray them as attractive. Antisocial behaviour such as bullying, crime, and
                discrimination must not be condoned (i.e., clearly criticised), and any dangerous imitable behaviour
                must not be glamorised.
              </p>
            </Col>
          </Row><br/>
          <Row>
            <Col md={2}><img src={require('./content_ratings/12+.png')} alt='12+' className='ratingLogo' /></Col>
            <Col md={10}>
              <h4><u>AGES 12+</u></h4>
              <p className='ratingSummary'>
                Content rated 12+ is not recommended for viewing by children under 12, as 12+ content may
                begin to deal with mature themes that may be too complex or distressing to children who have
                yet to reach adolescence. Language can be moderate and may even include the "f-word." Sexual
                content may also be moderate at 12+, although any depiction of sexual activity, which may
                involve nudity, must be brief and discreet. Violence and horror may be moderate as well. Sexual
                violence and suicide may begin to be addressed but only with mild detail suitable for young teens. 
                Drug use and teen drinking/smoking may be included infrequently. Antisocial behaviour such as
                discriminatory behaviour (which might consist of a few slurs) should be portrayed negatively by the
                content as a whole.
              </p>
            </Col>
          </Row><br/>
          <Row>
            <Col md={2}><img src={require('./content_ratings/15+.png')} alt='15+' className='ratingLogo' /></Col>
            <Col md={10}>
              <h4><u>AGES 15+</u></h4>
              <p className='ratingSummary'>
                15+ content is not recommended for viewing by minors under 15, as the treatment of themes and
                other various content in content rated 15+ can be mature. Language, violence, horror, and drug
                use can be strong. Sexual themes may also be strong and prevalent, although there will not be
                strong detail in depicting sexual activity or nudity. There may be greater focus in the darkest
                themes such as sexual violence, suicide, and self-harm, but the 15+ rating ensures that portrayal
                of such behaviour will not be detailed or prolonged. Antisocial behaviour, including
                discriminatory behaviour, may be included as long as the content as a whole does not suggest that
                there is nothing necessarily wrong or problematic with normalising such unacceptable behaviour.
              </p>
            </Col>
          </Row><br/>
          <Row>
            <Col md={2}><img src={require('./content_ratings/18+.png')} alt='18+' className='ratingLogo' /></Col>
            <Col md={10}>
              <h4><u>AGES 18+</u></h4>
              <p className='ratingSummary'>
                Content rated 18+ is restricted to adults 18 and up only because it usually consists of very intense,
                graphic, or offensive material that can disturb adults of any age. This may involve normalisation of extremely
                offensive or pornographic language, explicit sexual imagery, very strong gory images and violence that
                may be highly sadistic, glamorisation of drugs, or disturbingly detailed portrayal of behaviour associated
                with the darkest themes such as sexual violence, suicide, and self-harm. As disturbing these may be,
                18+ content should be freely available for adults to consume at their own free will as long as the content
                is not created illegally or contains themes that may reasonably risk serious harm to today's society (e.g.,
                overall endorsement or promotion of antisocial, discriminatory, illegal, violent, or life-risking behaviour).
                More detail about the what kind of content would be rejected can be found in the 18+ guidelines of the full
                Content Rating Guidelines available in the button above.
              </p>
            </Col>
          </Row>
        </Container>

      </animated.div>
    </div>
  );
}

export default ContentRatings;
